// Use client for interactions like check-in
'use client'; 

import { useEffect, useState, useTransition } from 'react';
import Image from 'next/image';
import { getEventById, getEventStatus, getMockUserById } from '@/lib/data';
import { checkInAction, getSessionUser } from '@/lib/actions';
import type { Event, Participant, EventStatus as TEventStatus } from '@/lib/types'; // Renamed to avoid conflict
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from '@/components/ui/card';
import UserAvatar from '@/components/user-avatar';
import { Badge } from '@/components/ui/badge';
import { CalendarDays, Users, KeyRound, MapPin, CheckCircle, Loader2, AlertTriangle, QrCode, UserCheck } from 'lucide-react';
import { format, parseISO } from 'date-fns';
import { useToast } from '@/hooks/use-toast';
import { Alert, AlertDescription, AlertTitle } from '@/components/ui/alert';
import ReactMarkdown from 'react-markdown'; // For rendering markdown, install with `npm install react-markdown`
import { ScrollArea } from '@/components/ui/scroll-area';

// If react-markdown is not allowed, replace with a simple preformatted display:
// const MarkdownDisplay = ({ content }: { content: string }) => <pre className="whitespace-pre-wrap font-sans text-sm">{content}</pre>;


interface EventPageParams {
  params: { id: string };
}

export default function EventDetailPage({ params }: EventPageParams) {
  const [event, setEvent] = useState<Event | null>(null);
  const [currentUser, setCurrentUser] = useState<Participant | null>(null);
  const [isLoading, setIsLoading] = useState(true);
  const [isCheckingIn, startCheckInTransition] = useTransition();
  const { toast } = useToast();

  useEffect(() => {
    async function fetchData() {
      setIsLoading(true);
      const fetchedEvent = await getEventById(params.id);
      
      if (fetchedEvent) {
        setEvent(fetchedEvent);
        const sessionUser = await getSessionUser();
        if (sessionUser) {
          const participantInfo = fetchedEvent.participants.find(p => p.id === sessionUser.id);
          setCurrentUser(participantInfo || { ...sessionUser, isCheckedIn: false });
        }
      }
      setIsLoading(false);
    }
    fetchData();
  }, [params.id]);

  const handleCheckIn = async () => {
    if (!event || !currentUser) return;

    startCheckInTransition(async () => {
      // Placeholder for Geetest CAPTCHA:
      // const geetestResponse = await showGeetestCaptcha(); // This would be an API call or SDK interaction
      // if (!geetestResponse.success) {
      //   toast({ title: "CAPTCHA Failed", description: "Please complete the human verification.", variant: "destructive" });
      //   return;
      // }
      // Pass geetestResponse.token to checkInAction if needed
      const result = await checkInAction(event.id); // Add geetest token if applicable
      if (result.success) {
        toast({ title: 'Check-in Successful!', description: result.message });
        // Update local state
        setEvent(prevEvent => prevEvent ? ({
          ...prevEvent,
          participants: prevEvent.participants.map(p => p.id === currentUser.id ? {...p, isCheckedIn: true} : p)
        }) : null);
        setCurrentUser(prevUser => prevUser ? {...prevUser, isCheckedIn: true} : null);
      } else {
        toast({ title: 'Check-in Failed', description: result.message, variant: 'destructive' });
      }
    });
  };
  
  if (isLoading) {
    return (
      <div className="flex justify-center items-center min-h-[calc(100vh-15rem)]">
        <Loader2 className="h-12 w-12 animate-spin text-primary" />
      </div>
    );
  }

  if (!event) {
    return (
      <div className="text-center py-10">
        <AlertTriangle className="mx-auto h-12 w-12 text-destructive" />
        <AlertTitle className="mt-4 text-2xl font-semibold">Event Not Found</AlertTitle>
        <AlertDescription className="mt-2 text-muted-foreground">
          The event you are looking for does not exist or has been removed.
        </AlertDescription>
        <Button onClick={() => window.history.back()} className="mt-6">Go Back</Button>
      </div>
    );
  }

  const eventStatus = getEventStatus(event.date);
  const formattedDate = format(parseISO(event.date), 'EEEE, MMMM d, yyyy \'at\' HH:mm');
  const canCheckIn = eventStatus === 'current' && currentUser && event.participants.some(p => p.id === currentUser.id) && !currentUser.isCheckedIn;
  const alreadyCheckedIn = currentUser?.isCheckedIn === true && event.participants.some(p => p.id === currentUser.id);


  return (
    <div className="max-w-4xl mx-auto space-y-8">
      <Card className="overflow-hidden shadow-xl">
        {event.imageUrl && (
          <div className="relative w-full h-64 md:h-80">
            <Image src={event.imageUrl} alt={event.name} layout="fill" objectFit="cover" data-ai-hint="event competition" />
          </div>
        )}
        <CardHeader className="pt-6">
          <div className="flex justify-between items-start">
            <CardTitle className="text-3xl md:text-4xl font-bold">{event.name}</CardTitle>
            <Badge variant={eventStatus === 'current' ? 'default' : eventStatus === 'upcoming' ? 'secondary' : 'outline'} 
                   className={`px-3 py-1.5 text-sm ${eventStatus === 'current' ? 'bg-green-500 text-white' : eventStatus === 'upcoming' ? 'bg-blue-500 text-white' : 'border-gray-500'}`}>
              {eventStatus.toUpperCase()}
            </Badge>
          </div>
          <CardDescription className="text-lg text-muted-foreground flex items-center pt-2">
            <CalendarDays className="mr-2 h-5 w-5" /> {formattedDate}
          </CardDescription>
        </CardHeader>
        <CardContent className="space-y-6">
          <div className="prose prose-sm sm:prose-base dark:prose-invert max-w-none bg-muted/30 p-4 rounded-md">
             {/* Using pre for basic markdown-like display. Replace with ReactMarkdown if allowed and installed. */}
             {/* <MarkdownDisplay content={event.description} /> */}
             <ReactMarkdown
                components={{
                  h1: ({node, ...props}) => <h1 className="text-2xl font-bold my-4" {...props} />,
                  h2: ({node, ...props}) => <h2 className="text-xl font-semibold my-3" {...props} />,
                  h3: ({node, ...props}) => <h3 className="text-lg font-semibold my-2" {...props} />,
                  p: ({node, ...props}) => <p className="my-2 leading-relaxed" {...props} />,
                  ul: ({node, ...props}) => <ul className="list-disc list-inside my-2 pl-4" {...props} />,
                  ol: ({node, ...props}) => <ol className="list-decimal list-inside my-2 pl-4" {...props} />,
                  li: ({node, ...props}) => <li className="my-1" {...props} />,
                  strong: ({node, ...props}) => <strong className="font-bold" {...props} />,
                  code: ({node, inline, className, children, ...props}) => {
                    const match = /language-(\w+)/.exec(className || '')
                    return !inline && match ? (
                     <pre className="bg-gray-800 text-white p-2 rounded my-2 overflow-x-auto"><code {...props}>{children}</code></pre>
                    ) : (
                      <code className="bg-gray-200 dark:bg-gray-700 px-1 py-0.5 rounded text-sm" {...props}>{children}</code>
                    )
                  }
                }}
              >{event.description}</ReactMarkdown>
          </div>

          <div className="grid grid-cols-1 md:grid-cols-2 gap-4 text-sm">
            <div className="flex items-center space-x-2 p-3 bg-secondary/20 rounded-md">
              <Users className="h-5 w-5 text-primary" />
              <span>Participants: {event.participants.length} / {event.participantLimit || 'Unlimited'}</span>
            </div>
            <div className="flex items-center space-x-2 p-3 bg-secondary/20 rounded-md">
              <KeyRound className="h-5 w-5 text-primary" />
              <span>CD-Key: <Badge variant="outline" className="text-base">{event.cdKey}</Badge></span>
            </div>
          </div>

          {currentUser && (
            <div className="pt-4">
              {canCheckIn && (
                <Card className="bg-accent/20 border-accent shadow-md">
                  <CardHeader>
                    <CardTitle className="flex items-center"><QrCode className="mr-2 h-6 w-6 text-accent-foreground"/> Event Check-in</CardTitle>
                    <CardDescription>This event is currently active. Click below to check in.</CardDescription>
                  </CardHeader>
                  <CardContent>
                    {/* Placeholder for Geetest CAPTCHA UI */}
                    <div className="my-4 p-4 border-dashed border-2 border-muted-foreground rounded-md text-center text-muted-foreground">
                      Geetest CAPTCHA would appear here. <br/> (Skipped for this mock version)
                    </div>
                    <Button onClick={handleCheckIn} disabled={isCheckingIn} className="w-full bg-accent text-accent-foreground hover:bg-accent/90">
                      {isCheckingIn ? <Loader2 className="mr-2 h-4 w-4 animate-spin" /> : <CheckCircle className="mr-2 h-4 w-4" />}
                      Check In Now
                    </Button>
                  </CardContent>
                </Card>
              )}
              {alreadyCheckedIn && (
                 <Alert variant="default" className="bg-green-100 dark:bg-green-900 border-green-500 dark:border-green-700">
                  <UserCheck className="h-5 w-5 text-green-700 dark:text-green-300" />
                  <AlertTitle className="text-green-800 dark:text-green-200">You are Checked In!</AlertTitle>
                  <AlertDescription className="text-green-700 dark:text-green-300">
                    You have successfully checked in for this event. Good luck!
                  </AlertDescription>
                </Alert>
              )}
              {eventStatus !== 'current' && currentUser && event.participants.some(p => p.id === currentUser.id) && (
                 <Alert variant="default">
                  <UserCheck className="h-4 w-4" />
                  <AlertTitle>Check-in Status</AlertTitle>
                  <AlertDescription>
                    Check-in for this event is not currently active.
                  </AlertDescription>
                </Alert>
              )}
            </div>
          )}

          <div>
            <h3 className="text-xl font-semibold mb-3">Participants ({event.participants.length})</h3>
            {event.participants.length > 0 ? (
              <ScrollArea className="h-72 rounded-md border">
                <ul className="divide-y divide-border">
                  {event.participants.map(participant => (
                    <li key={participant.id} className="flex items-center justify-between p-3 hover:bg-muted/50">
                      <div className="flex items-center space-x-3">
                        <UserAvatar user={participant} className="h-10 w-10" />
                        <span className="font-medium">{participant.username}</span>
                      </div>
                      {participant.isCheckedIn && <Badge variant="default" className="bg-primary text-primary-foreground">Checked In</Badge>}
                    </li>
                  ))}
                </ul>
              </ScrollArea>
            ) : (
              <p className="text-muted-foreground">No participants have joined yet.</p>
            )}
          </div>
        </CardContent>
        <CardFooter>
            <Button variant="outline" onClick={() => window.history.back()} className="w-full">
                Back to Events
            </Button>
        </CardFooter>
      </Card>
    </div>
  );
}
